@import '_common.scss';

main{
    position: relative;
}
.home-content1{
    height: calc(100vh - 160px);
    position: relative;
    a span{
        color: $sc;
        font-size: 24px;
        position: absolute;
        right: 20px; top: 20vh;
    }
    a:hover span{
        text-decoration: underline;
    }
    .main-title{
        padding-top: 24vh;
        span, p span{
            color: $st;
            font-size: 80px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        h3 {
            height: 140px;
            display: inline-block;
            overflow: hidden;
            transform: translateY(24px);
            p{
            color: $mc;
            font-size: 116px;
            font-weight: 700;
            letter-spacing: 0.04em;
            animation: text 8s infinite;
            }
        }
        p{
            font-size: 116px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        sup{
            color: $sc;
            font-size: 40px;
            font-weight: 700;
            letter-spacing: 0;
            line-height: 100px;
        }
    }
}
// 메인텍스트 돌아가는 애니메이션
@keyframes text{
    0%, 20%{ transform:translateY(0) }
    30%,50%{ transform:translateY(-100%) }
    60%,80%{ transform:translateY(-200%) }
    100%{ transform:translateY(-300%) }
}
// 스크롤 아이콘 움직이는 애니메이션
@keyframes scroll{
    0% {transform: translateY(0px);}
    50% {transform: translateY(-80px);}
    100% {transform: translateY(0px);}
}

// ================================================================================
// ============================= about ============================================
// ================================================================================

.scroll{
    position: fixed;
    left: calc(50% - 470px); bottom: 160px;
    img{
        animation: scroll 3s infinite;
    }
}
.cv{
    position: relative;
    .cv-name{
        position: absolute;
        left: 128px; top: 0;
        p{
            font-size: 80px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        sup{
            color: $sc;
            font-size: 40px;
            font-weight: 700;
            letter-spacing: 0;
            line-height: 100px;
        }
        p:nth-of-type(2){
            margin-left: 32px;
            color: $mc;
            font-size: 60px;
        }
    }
    .cv-profile{
        width: 848px;
        margin: 180px 0 192px 432px;
    }
    .cv-email{
        position: absolute;
        right: 38px; bottom: -84px;
        p{
            font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            font-size: 24px;
            font-weight: 700;
            letter-spacing: 0.02em;
            line-height: 1.4;
        }
        p:nth-of-type(1){
            color: $st;
        }
        p:nth-of-type(2){
            text-transform: lowercase;
        }
        &::before{
            content: '';
            height: 54px;
            border-right: 4px solid $sc;
            position: absolute;
            right: 288px; bottom: 6px;
        }
    }
}
.slide-ani{
    div{
        height: 80px;
        margin-bottom: 20px;
        border-top: 1px solid $st;
        border-bottom: 1px solid $st;
        overflow: hidden;
    }
    p{
        margin-top: 8px;
        display: inline-block;
        white-space: nowrap;
        font-size: 48px;
        font-weight: 700;
        color: $st;
    }
    i{
        font-size: 48px;
        font-weight: 700;
        color: $mc;
    }
    b{
        font-size: 48px;
        font-weight: 700;
        color: $sc;
    }
    .slide1{
        animation: slide1 20s linear infinite;
    }
    @keyframes slide1 {
        from   { transform: translate(0%);}
        99%,to { transform: translate(-20%);}
    }
    .slide2{
        animation: slide2 20s linear infinite;
    }
    @keyframes slide2 {
        from   { transform: translate(-20%);}
        99%,to { transform: translate(0%);}
    }
}